import { Routes } from "@angular/router";
import { ClubHistoryComponent, RulesComponent, AboutClubComponent, CoachTeamComponent, SquadComponent } from "./index";

export const homeRoutes: Routes = [
    { path: "clubHistory", component: ClubHistoryComponent, data: { title: " " } },
    { path: "coachTeam", component: CoachTeamComponent, data: { title: " " } },
    { path: "rules", component: RulesComponent, data: { title: "" } },
    { path: "aboutClub", component: AboutClubComponent, data: { title: " " } },
    { path: "squad", component: SquadComponent, data: { title: " " } }
];


